.resume-body {
    min-width: 700px;
    .topheader {
        text-align: center;
    }

    .content {
        padding-left: 30px; 
        padding-right: 30px;
        .header {
            font-size: 14px;
            text-decoration: underline;
            margin-bottom: 5px;
            font-weight: 900;
        }
        
        .title {
            font-size: 14px;
            font-weight: 900;
        }
        
        .row {
            font-size: 13px;
            display: flex;
        }
        
        .column-left {
            flex: 50%;
            text-align: left;
            min-width: 400px;
        }
        
        .column-right {
            flex: 50%;
            text-align: right;
            padding-right: 30px;
        }
    }
    
    ul {
        margin-top: 5px;
    }
    
    li {
        font-size: 13px;
        padding-bottom: 2px;
    }
}